import Vue from "vue";
import VueRouter from "vue-router";
//import NProgress from 'vue-nprogress'

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: () => 
      import("../views/Home.vue")
  },
  {
    path: "/notice",
    name: "notice",
    component: () =>
      import("../views/Notice.vue")
  },
  {
    path: "/player",
    name: "player",
    component: () =>
      import("../views/Player.vue")
  },
  {
    path: "/direct/player/:player_id",
    name: "direct_player",
    component: () =>
      import("../views/DirectPlayer.vue")
  },
  {
    path: "/hero",
    name: "hero",
    component: () =>
      import("../views/Hero.vue")
  },
  {
    path: "/minion",
    name: "minion",
    component: () =>
      import("../views/Minion.vue")
  },
  {
    path: "/race",
    name: "race",
    component: () =>
      import("../views/Race.vue")
  },
  {
    path: "/gameDetail",
    name: "gameDetail",
    component: () =>
      import("../views/GameDetail.vue")
  },
  {
    path: "/gameDetail2",
    name: "gameDetail2",
    component: () =>
      import("../views/GameDetail2.vue")
  },
  {
    path: "/static",
    name: "static",
    component: () =>
      import("../views/Static.vue")
  },
  {
    path: "/manual",
    name: "manual",
    component: () =>
      import("../views/Manual.vue")
  }
];

const router = new VueRouter({
  mode: "history",
  base: process.env.BASE_URL,
  scrollBehavior (to, from, savedPosition) {
    if (savedPosition) {
      return savedPosition
    } else {
      return { x: 0, y: 0 }
    }
  },
  routes
});
/*
router.beforeResolve((to, from, next) => {
  // If this isn't an initial page load.
  if (to.name) {
      // Start the route progress bar.
      NProgress.start()
  }
  next()
})

router.afterEach((to, from) => {
  // Complete the animation of the route progress bar.
  NProgress.done()
})
*/

export default router;
